/** 获取载体相对与窗口的坐标 宽高等信息 */
export const getDomInfo = (dom) => {
    if (!dom) {
        return {};
    }
    function getScrollTop(obj) {
        let tmp = obj.scrollTop;
        let val = obj.parentElement;
        while (val !== null) {
            tmp += val.scrollTop;
            val = val.parentElement;
        }
        return tmp;
    }
    function getScrollLeft(obj) {
        let tmp = obj.scrollLeft;
        let val = obj.parentElement;
        while (val !== null) {
            tmp += val.scrollLeft;
            val = val.parentElement;
        }
        return tmp;
    }
    const domInfo = {
        x: dom.offsetLeft - getScrollLeft(dom),
        y: dom.offsetTop - getScrollTop(dom),
        width: dom.clientWidth,
        height: dom.clientHeight,
    };
    return domInfo;
};
